import React from 'react';
import { Handle, Position } from 'reactflow';
import { shallow } from 'zustand/shallow';
import { Slider, Select, Divider, ConfigProvider } from 'antd';
import { useStore } from '../tools/store';
import styles from './styles.less';

const colorPrimary = '#fa541c';

const selector = (id: any) => (store: any) => ({
  setOscFrequency: (value: number) => store.updateNode(id, { frequency: value }),
  setOscType: (value: string) => store.updateNode(id, { type: value }),
});

const Osc = ({ id, data }: any) => {
  const { setOscFrequency, setOscType } = useStore(selector(id), shallow);
  return (
    <div className={styles.node}>
      {/* <Handle type="target" position={Position.Top} /> */}
      <div className={styles.card} style={{ backgroundColor: colorPrimary }}>
        <p className={styles.header}>振荡器</p>
        <ConfigProvider
          theme={{ token: { colorPrimary } }}
        >
          <div className={styles.panel}>
            <div className={styles.title}>
              <h4>频率</h4>
              <span>{`${data.frequency}赫兹`}</span>
            </div>
            <div>
              <Slider
                className="nodrag"
                value={data.frequency}
                min={10}
                max={1000}
                onChange={setOscFrequency}
              />
            </div>
            <Divider style={{ margin: '4px 0' }} />
            <h4>波形</h4>
            <div>
              <Select
                className="nodrag"
                value={data.type}
                onChange={setOscType}
                size="small"
                style={{ width: '100%' }}
                getPopupContainer={triggerNode => triggerNode.parentNode.parentNode.parentNode}
                popupMatchSelectWidth={false}
                placement="topLeft"
                options={[
                  { value: 'sine', label: '正弦波' },
                  { value: 'triangle', label: '三角波' },
                  { value: 'sawtooth', label: '锯齿波' },
                  { value: 'square', label: '方波' },
                ]}
              />
            </div>
          </div>
        </ConfigProvider>
      </div>
      <Handle type="source" position={Position.Bottom} />
    </div>
  );
};

export default Osc;
