import React, { useState } from 'react';
import { Button, Drawer, Card } from 'antd';
import { QuestionCircleOutlined, CloseOutlined } from '@ant-design/icons';
import { getMorseCodesList } from '@/utils/morse/charCodes';
import getOptions from '@/utils/morse/options';

const App: React.FC = () => {
  const [open, setOpen] = useState(false);

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  const gridTitleStyle: React.CSSProperties = {
    width: '100%',
    textAlign: 'center',
    fontWeight: 600,
  };

  const gridItemKeyStyle: React.CSSProperties = {
    width: '10%',
    textAlign: 'center',
  };
  const gridItemValueStyle: React.CSSProperties = {
    width: '15%',
    textAlign: 'center',
  };
  const morseCodesObj = getMorseCodesList(getOptions({}));
  const morseCodesTitleMap: any = {
    English: '英文',
    Number: '数字',
    Symbol: '标点',
  };

  return (
    <>
      <Button size="large" type="text" icon={<QuestionCircleOutlined />} onClick={showDrawer}>介绍说明</Button>
      <Drawer
        title="介绍说明"
        getContainer={false}
        placement={"right"}
        width={"100%"}
        onClose={onClose}
        closeIcon={<Button type="text" icon={<CloseOutlined />} onClick={onClose}>关闭</Button>}
        open={open}
      >
        <div style={{ textAlign: 'left' }}>
          <h2>摩斯码翻译制作工具使用方法</h2>
          <p>摩斯密码全称摩尔斯电码，是由美国人摩尔斯在1837年发明。从外表来看，它是由点dot（.）划dash（-）这两种符号所组成的一组时通时断的字符串。摩尔斯码在早期的无线电上作用是非常大的，它是每个无线电通讯者所须必知的。由于它所占的频宽最少，同时又具有一种技术及艺术的特性，所以在实际的生活当中就有着广泛的应用。</p>
          <br />
          <h2>摩斯码工具注意事项</h2>
          <p>一、因为国际标准摩斯密码中并不存在中文字符，所以本站的中文摩斯密码加密解密工具使用了中文Unicode编码进行转换得到摩斯码。如果您使用了本工具对中文字符进行了摩斯码加密，建议同样使用本工具对其进行解密，因为大部分网站的摩斯密码转换工具不支持中文编码！并且还有部分生成的是伪摩斯密码！</p>
          <p>二、该摩斯码翻译制作工具对部分特殊符号可能无法正常加密或解密，因此会将它们自动舍弃！</p>
          <p>三、只支持斜杠"/"和空格两种密文分隔符号，因为这两种摩斯码分隔符最常用！</p>
          <br />
          <h2>摩斯码对照表</h2>
          <Card style={{ borderRadius: '2px', overflow: 'hidden' }}>
            {morseCodesObj.map(obj => {
              const list = [];
              list.push(<Card.Grid key={obj.type} hoverable={false} style={gridTitleStyle}>{morseCodesTitleMap[obj.type]}</Card.Grid>);
              (obj.items || []).forEach((item: any) => {
                list.push(<Card.Grid key={`${item.id}-key`} hoverable={false} style={gridItemKeyStyle}>{item.key}</Card.Grid>);
                list.push(<Card.Grid key={`${item.id}-value`} hoverable={false} style={gridItemValueStyle}>{item.value}</Card.Grid>);
              });
              return list;
            })}
          </Card>
          <br />
        </div>
      </Drawer>
    </>
  );
};

export default App;
