import React from 'react';
import { Handle, Position } from 'reactflow';
import { shallow } from 'zustand/shallow';
import { Slider, ConfigProvider } from 'antd';
import { useStore } from '../tools/store';
import styles from './styles.less';

const colorPrimary = '#faad14';

const selector = (id: any) => (store: any) => ({
  setGain: (value: number) => store.updateNode(id, { gain: value }),
});

const Amp = ({ id, data }: any) => {
  const { setGain } = useStore(selector(id), shallow);
  return (
    <div className={styles.node}>
      <Handle type="target" position={Position.Top} />
      <div className={styles.card} style={{ backgroundColor: colorPrimary }}>
        <p className={styles.header}>
          增益器
        </p>
        <ConfigProvider
          theme={{ token: { colorPrimary } }}
        >
          <div className={styles.panel}>
            <div className={styles.title}>
              <h4>音量</h4>
              <span>{data.gain}</span>
            </div>
            <div>
              <Slider
                className="nodrag"
                value={data.gain}
                min={0}
                max={1}
                step={0.01}
                onChange={setGain}
              />
            </div>
          </div>
        </ConfigProvider>
      </div>
      <Handle type="source" position={Position.Bottom} />
    </div>
  );
};

export default Amp;
