const context = new AudioContext();
const nodes = new Map();

// const osc = context.createOscillator();
// const amp = context.createGain();
// const out = context.destination;

export const isRunning = () => {
  return context.state === 'running';
};

export const toggleAudio = () => {
  return isRunning() ? context.suspend() : context.resume();
};

export const connect = (sourceId: string, targetId: string) => {
  console.log('nodes', nodes);
  const source = nodes.get(sourceId);
  const target = nodes.get(targetId);
  console.log('connect', source, target);
  source.connect(target);
};

export const disconnect = (sourceId: string, targetId: string) => {
  const source = nodes.get(sourceId);
  const target = nodes.get(targetId);
  source.disconnect(target);
};

export const createAudioNode = (id: string, type: 'osc' | 'amp' | 'out', data: any) => {
  if (type === 'osc') {
    const osc = context.createOscillator();
    osc.frequency.value = data.frequency;
    osc.type = data.type;
    osc.start();
    nodes.set(id, osc);
  } else if (type === 'amp') {
    const amp = context.createGain();
    amp.gain.value = data.gain;
    nodes.set(id, amp);
  } else if (type === 'out') {
    const out = context.destination;
    nodes.set(id, out);
  }
};

export const updateAudioNode = (id: string, data: any) => {
  const node = nodes.get(id);
  for (const [key, value] of Object.entries(data)) {
    if (node[key] instanceof AudioParam) {
      node[key].value = value;
    } else {
      node[key] = value;
    }
  }
};

export const removeAudioNode = (id: string) => {
  const node = nodes.get(id);
  node.disconnect();
  node.stop?.();
  nodes.delete(id);
};
