import { create } from 'zustand';
import { applyNodeChanges, applyEdgeChanges } from 'reactflow';
import { guid } from './utils';
import * as audioUtils from './audio';

export const useStore = create((set: any, get: any) => ({
  nodes: [
  ],
  edges: [
  ],
  // onNodesChange 处理 节点 移动、删除
  onNodesChange(changes: any) {
    set({
      nodes: applyNodeChanges(changes, get().nodes),
    });
  },
  // onEdgesChange 处理 连接线 移动、删除
  onEdgesChange(changes: any) {
    set({
      edges: applyEdgeChanges(changes, get().edges),
    });
  },
  // 是否播放
  isRunning: false,
  // 切换播放状态
  toggleAudio() {
    audioUtils.toggleAudio().then(() => {
      set({ isRunning: !get().isRunning });
    });
  },
  // 初始化节点数据
  initNodes() {
    const defaultData = [
      {
        id: guid(),
        type: 'osc',
        data: { frequency: 220, type: 'square' },
        position: { x: 200, y: 0 }
      },
      {
        id: guid(),
        type: 'amp',
        data: { gain: 0.5 },
        position: { x: 200, y: 180 }
      },
      {
        id: guid(),
        type: 'out',
        data: {},
        position: { x: 200, y: 300 }
      },
    ];
    defaultData.forEach((item: any) => {
      audioUtils.createAudioNode(item.id, item.type, item.data);
    });
    set({ nodes: [...defaultData] });
  },
  // 更新节点的数据
  updateNode(id: any, data: any) {
    audioUtils.updateAudioNode(id, data);
    set({
      nodes: get().nodes.map((node: any) =>
        node.id === id
          ? { ...node, data: Object.assign(node.data, data) }
          : node
      )
    });
  },
  // 新增节点的数据
  createNode(type: 'osc' | 'amp' | 'out') {
    if (type === 'osc') {
      const id = guid();
      const data = { frequency: 200, type: 'sine' };
      const position = { x: 0, y: 0 };
      audioUtils.createAudioNode(id, type, data);
      set({
        nodes: [...get().nodes, { id, type, data, position }]
      });
    } else if (type === 'amp') {
      const id = guid();
      const data = { gain: 0.5 };
      const position = { x: 0, y: 0 };
      audioUtils.createAudioNode(id, type, data);
      set({
        nodes: [...get().nodes, { id, type, data, position }]
      });
    } else if (type === 'out') {
      const id = guid();
      const data = {};
      const position = { x: 0, y: 0 };
      audioUtils.createAudioNode(id, type, data);
      set({
        nodes: [...get().nodes, { id, type, data, position }]
      });
    }
  },
  // createEdge 连接两个节点
  createEdge(data: any) {
    console.log('createEdge', data);
    const id = guid();
    const edge = { id, animated: true, ...data };
    audioUtils.connect(edge.source, edge.target);
    set({ edges: [edge, ...get().edges] });
  },
  // 删除节点
  removeNode(data: any) {
    console.log(data);
    for (const { id } of data) {
      audioUtils.removeAudioNode(id);
    }
  },
  // 删除连接点
  removeEdge(data: any) {
    console.log('removeEdge', data);
    for (const item of data) {
      const { source, target } = item;
      audioUtils.disconnect(source, target);
    }
  },
}));

