import React from 'react';
import { Handle, Position } from 'reactflow';
import { shallow } from 'zustand/shallow';
import { Button, ConfigProvider } from 'antd';
import { CaretRightOutlined, PauseOutlined } from '@ant-design/icons';
import { useStore } from '../tools/store';
import styles from './styles.less';

const colorPrimary = '#13c2c2';

const selector = (store: any) => ({
  isRunning: store.isRunning,
  toggleAudio: store.toggleAudio,
});

const Out = ({ id, data }: any) => {
  const { isRunning, toggleAudio } = useStore(selector, shallow);
  return (
    <div className={styles.node}>
      <Handle type="target" position={Position.Top} />
      <div className={styles.card} style={{ backgroundColor: colorPrimary }}>
        <p className={styles.header}>播放器</p>
        <ConfigProvider
          theme={{ token: { colorPrimary } }}
        >
          <div className={styles.panel}>
            <Button
              className="nodrag"
              icon={isRunning ? <PauseOutlined /> : <CaretRightOutlined />}
              onClick={toggleAudio}
              style={{ width: '100%' }}
            >
              {isRunning ? '停止' : '播放'}
            </Button>
          </div>
        </ConfigProvider>
      </div>
    </div>
  );
};

export default Out;
