import React from 'react';
import { Button, Space, Tabs, ConfigProvider } from 'antd';
import { shallow } from 'zustand/shallow';
import { useStore } from '../tools/store';
import styles from './index.less';

const selector = (store: any) => ({
  createNode: store.createNode,
  nodes: store.nodes,
  edges: store.edges,
});

const Sider = () => {
  const store = useStore(selector, shallow);
  return (
    <div className={styles.sider}>
      <Space direction="vertical" style={{ padding: '10px 20px' }}>
        <ConfigProvider theme={{ token: { colorPrimary: '#fa541c' } }}>
          <Button style={{ width: '100%' }} type="primary" onClick={() => store.createNode('osc')}>添加 振荡器</Button>
        </ConfigProvider>
        <ConfigProvider theme={{ token: { colorPrimary: '#faad14' } }}>
          <Button style={{ width: '100%' }} type="primary" onClick={() => store.createNode('amp')}>添加 增益器</Button>
        </ConfigProvider>
        {/* <Button onClick={() => store.createNode('out')}>添加 播放器</Button> */}
      </Space>
      <Tabs
        tabPosition="top"
        centered
        items={[
          {
            key: '1',
            label: '节点数据',
            children: (
              <div>
                <pre>
                  {JSON.stringify(store.nodes, null, 2)}
                </pre>
              </div>
            ),
          },
          {
            key: '2',
            label: '连接数据',
            children: (
              <div>
                <pre>
                  {JSON.stringify(store.edges, null, 2)}
                </pre>
              </div>
            ),
          },
        ]}
      />
    </div>
  );
};

export default Sider;
